import React, { Component } from 'react';
import { PaymentForm, Payment } from './PaymentForm';
import { v4 as uuidv4 } from 'uuid';
import { Auth, AuthEvents, CurrentUserState } from '../CurrentUser';
import Skeleton from '@mui/material/Skeleton';
import dayjs from 'dayjs';
import { DebitEvent, RecurrenceEnum } from '../../client/client';
import Cookies from 'js-cookie';
import { enqueueSnackbar } from 'notistack';
import FormControl from '@mui/material/FormControl';
import IconButton from '@mui/material/IconButton';
import AddIcon from '@mui/icons-material/Add';

type PaymentsHash = {
  [id: string] : Payment;
}

export enum DataPath {
  Incomes = '/api/debit-event/',
  Expenses = '/api/credit-event/',
}


type PaymentsListState = {
  paymentsOrder:  string[], // list of IDs to retain the order
  payments: PaymentsHash,
  auth?: Auth,
  isLoaded: boolean
}

type  PaymentsListProperties =  {
  baseUrl: string;
  dataPath: DataPath;
}
// following is a candidate for the properties

function incomeToPayment(payment: DebitEvent): Payment{
  return {
    amount: parseFloat(payment.total),
    name: payment.name, 
    frequency: payment.recurrence,
    date: dayjs(payment.first_event_date)
  }
}

export class PaymentsList extends Component<PaymentsListProperties, PaymentsListState> {
  state: PaymentsListState = {
    paymentsOrder: [uuidv4(), ],
    payments: {},
    isLoaded: false
  }

  async componentDidMount(): Promise<void> {
    console.log('PaymentsList:componentDidMount');
    //loading the data
    this.loadData();


    // 2 options here load from server,  or load from local storage ... 
    // going classic way but it may be a mistake....  
    // data can be loaded on the background after user logging in
    window.addEventListener(AuthEvents.AUTH_SET, this.onAuthSet.bind(this) as EventListener);
  }

  async loadData(): Promise<void> {
      const res  = await fetch(this.props.baseUrl + this.props.dataPath);
      const respBody = await res.json();
      let newPayments: PaymentsHash = {};
      let newPaymentsOrder: string[] = [];

      respBody.results.map((payment: DebitEvent) => {
        newPaymentsOrder.push(payment.id!!);
        newPayments[payment.id!!] = incomeToPayment(payment);
        return null;
      });

      this.setState({
        ...this.state,
        isLoaded: true,
        paymentsOrder: newPaymentsOrder,
        payments:  newPayments
      });
  }
  async savePayment(id: string, payment: Payment): Promise<void> {
    console.log('saving payment', id, payment);
    const response = await fetch(
      this.props.baseUrl + this.props.dataPath + id + '/',
      {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json', 
          'X-CSRFToken': Cookies.get('csrftoken') ?? '',
        },
        body: JSON.stringify({
          total: payment.amount,
          name: payment.name,
          recurrence: payment.frequency,
          first_event_date: payment.date?.format()
        })
      }
    );
    if(response.status !== 200) {
      console.error('unexpected error', response.status, response )
    }
  }

  async removePayment(id: string): Promise<void> {
    console.log('remove payment', id);
    const response = await fetch(
      this.props.baseUrl + this.props.dataPath + id + '/',
      {
        method: 'DELETE',
        headers: {
          'Content-Type': 'application/json', 
          'X-CSRFToken': Cookies.get('csrftoken') ?? '',
        }
      }
    );

    if(response.status !== 204) {
      enqueueSnackbar('unexpected error' + response.status + ': ' + await response.text() , { variant: 'error' });
    }else {
      enqueueSnackbar('entry removed' , { variant: 'success' });
    }
  }
  async addEmptyPayment(): Promise<[string, Payment]> {
    const response = await fetch(
      this.props.baseUrl + this.props.dataPath,
      {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json', 
          'X-CSRFToken': Cookies.get('csrftoken') ?? '',
        },
        body: JSON.stringify({
          total: "0",
          name: "",
          recurrence: RecurrenceEnum.None,
          first_event_date: dayjs().format()
        })
      }
    );

    if(response.status !== 201) {
      console.error('unexpected error', response.status, response);
      enqueueSnackbar('unexpected error' + response.status + ': ' + response.text() , { variant: 'error' });
    }

    const responseBody =  await response.json();
    return [responseBody['id'], incomeToPayment(responseBody as DebitEvent)];
  }

  onAuthSet(event: CustomEvent<CurrentUserState>) {
    console.log('PaymentsList: onAuthSet')
    const auth: Auth = {
      isAuthenticated: event.detail.isAuthenticated ?? false,
    }
    this.setState({...this.state, auth});
  }

  async onPaymentChange(id: string, payment: Payment) {

    let paymentsSet = this.state.payments;
    paymentsSet[id] = payment;
    this.setState({...this.state, payments: paymentsSet})
    this.savePayment(id, payment);
  }

  onPaymentRemove(id: string) {
    let currentPaymentsOrder = this.state.paymentsOrder;
    let paymentPos = currentPaymentsOrder.indexOf(id);
    if (paymentPos > -1) {
      currentPaymentsOrder.splice(paymentPos, 1);
      this.setState({
        ...this.state,
        paymentsOrder: currentPaymentsOrder
        });
      }
      this.removePayment(id);
  }

  async onPaymentAddAfter(id: string) {
    let currentPaymentsOrder = this.state.paymentsOrder;
    let paymentPos = currentPaymentsOrder.indexOf(id);
    const newPaymentId = uuidv4();
    currentPaymentsOrder.splice(paymentPos + 1, 0, newPaymentId);
    // questionable operation.. consider it later.
    // this.setState({
    //   ...this.state,
    //   paymentsOrder: currentPaymentsOrder
    // }
    // );
    const savedPayment = await this.addEmptyPayment();
    const realId = savedPayment[0];
    const realPayment = savedPayment[1];
    currentPaymentsOrder[currentPaymentsOrder.indexOf(newPaymentId)] = realId;
    const currentPayments = this.state.payments;
    currentPayments[realId] = realPayment;
    this.setState({...this.state, 
      payments: currentPayments, 
      paymentsOrder: currentPaymentsOrder
    });
  }

  render(): React.ReactNode {
    return <>
      <FormControl sx={{ m: 1 }} size="medium"  variant="outlined" >
          <IconButton aria-label="Add" onClick={() => this.onPaymentAddAfter("1")}>
            <AddIcon fontSize="inherit" />
          </IconButton>
        </FormControl>
        { this.state.isLoaded ? this.state.paymentsOrder.map((id) => (
          <PaymentForm 
            key={id} 
            onChange={this.onPaymentChange.bind(this)} 
            id={id} 
            onRemove={this.state.paymentsOrder.length > 1 ? this.onPaymentRemove.bind(this): undefined} 
            onAdd={this.onPaymentAddAfter.bind(this)} 
            initialPayment={this.state.payments[id] ?? undefined}/>
          )) : <>
          {Array.from(Array(5)).map((_, ind)=> (<Skeleton key={ind} variant="rounded" width={610} height={30} sx={{ m: 1 }} />))}
          </>}
    </>
  }
}

