import React from 'react';
import { SnackbarProvider } from 'notistack';
import {
  createBrowserRouter,
  RouterProvider,
  // Link,
  // Outlet - should hold  the current path 
} from "react-router-dom";


import './App.css';
import { Index } from './components/routes/Index';
import { CurrentUser } from './components/CurrentUser';
import { Root } from './components/routes/Root';
import { Incomes } from './components/routes/plan/Incomes';
import { Expenses } from './components/routes/plan/Expenses';
import { Forecast } from './components/routes/plan/Forecast';



const baseUrl = process.env.NODE_ENV === 'development' ? '/django' : '';

const router = createBrowserRouter([
  {
    path: "/",
    element:<Root baseUrl={baseUrl} />,
    children: [
      {
        path: "",
        element: <Index baseUrl={baseUrl} />
      },
      {
        path: "plan/",
        // element: <Plan baseUrl={baseUrl} />,
        children: [
          {
            path: "incomes/",
            element: <Incomes baseUrl={baseUrl} />
          },
          {
            path: "expenses/",
            element: <Expenses baseUrl={baseUrl} />
          },
          {
            path: "forecast/",
            element: <Forecast baseUrl={baseUrl} />
          }
        ]
      },

    ]
  },
  
]);

function App() {
  return (<div>
    <CurrentUser baseUrl={baseUrl}/>
    <SnackbarProvider>
      <RouterProvider router={router} />
    </SnackbarProvider>
  </div>);
}

export default App;