import React, { Component } from 'react';

import Box from '@mui/material/Box';
import FormControl from '@mui/material/FormControl';
import InputAdornment from '@mui/material/InputAdornment';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import OutlinedInput from '@mui/material/OutlinedInput';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import DriveFileRenameOutlineIcon from '@mui/icons-material/DriveFileRenameOutline';
import TimerIcon from '@mui/icons-material/Timer';
import AddIcon from '@mui/icons-material/Add';
import DeleteIcon from '@mui/icons-material/Delete';
import IconButton from '@mui/material/IconButton';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import { LocalizationProvider } from '@mui/x-date-pickers';
import { DatePicker } from '@mui/x-date-pickers';
import TextField from '@mui/material/TextField';
import { Dayjs } from 'dayjs';
import 'dayjs/locale/en-au';
import { RecurrenceEnum } from '../../client/client';


export type Payment = {
  amount?: number;
  name?: string;
  frequency?: RecurrenceEnum;
  date?: Dayjs;
 }

 
type PaymentFormProp = {
  // onAmountChange: (payment: Payment) => void;
  initialPayment?: Payment;
  id: string,
  onChange: (id: string, payment: Payment) => Promise<void>;
  onAdd?: (id: string) => void;
  onRemove?: (id: string) => void;
}; 

type PaymentFormState = {
  payment: Payment
}


export class PaymentForm extends Component<PaymentFormProp, PaymentFormState> {
  state: PaymentFormState = {    
    payment: {} // figure out how to copy values propagated from above
  };
  constructor(props: PaymentFormProp) {
    super(props);
    console.log('PaymentForm:constructor');
    this.state = {payment: props.initialPayment ?? {}};
  }
  
  onAmountChange(event: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) {
    const newPayment = {
      ...this.state.payment,
      amount: parseInt(event.target.value)
    };
    this.setState({
      payment: newPayment
    });
    this.props.onChange(this.props.id, newPayment);
  }
  onNameChange(event: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) {
    const newPayment = {
      ...this.state.payment,
      name: event.target.value
    };

    this.setState({
      payment: newPayment
    });

    this.props.onChange(this.props.id, newPayment);
  }
  onFrequencyChange(event: SelectChangeEvent<RecurrenceEnum>, child: React.ReactNode) {
    const newPayment = {
      ...this.state.payment,
      frequency: event.target.value as RecurrenceEnum
    };

    this.setState({
      payment: newPayment
    });
    this.props.onChange(this.props.id, newPayment);
  }
  onDateChange(value: Dayjs | null) {
    const newPayment = {
      ...this.state.payment,
      date: value ?? undefined
    };

    this.setState({
      payment: newPayment
    });

    this.props.onChange(this.props.id, newPayment);
  }

  // todo: consider moving localization provider to the very top of hierarchy
  render(): React.ReactNode {
    return <>
    <LocalizationProvider adapterLocale={'en-au'} dateAdapter={AdapterDayjs} >
    <Box 
      component="form"
      noValidate
      autoComplete="off" 
      sx={{ m: 2 }}
      >
      <div>
        <FormControl sx={{ minWidth: 120, m: 1 }} size="small"  variant="outlined" >
          <InputLabel htmlFor={"amount-" + this.props.id}>Amount</InputLabel>
          <OutlinedInput
            id={"amount-" + this.props.id}
            startAdornment={<InputAdornment position="start">$</InputAdornment>} /// TBD: will be nice to have money formatting aka 10`000
            label="Amount"
            value={this.state.payment?.amount ?? ''}
            inputProps={{ inputMode: 'numeric', pattern: '[0-9]*'}}
            type="number"
            onChange={this.onAmountChange.bind(this)}
          />
        </FormControl>
        <FormControl sx={{ minWidth: 120, m:1 }} size="small"  variant="outlined">
          <InputLabel htmlFor={"name-" + this.props.id}>Name</InputLabel>
          <OutlinedInput
            id={"name-" + this.props.id}
            startAdornment={<InputAdornment position="start">
              <DriveFileRenameOutlineIcon />
            </InputAdornment>}
            value={this.state.payment?.name ?? ''}
            onChange={this.onNameChange.bind(this)}
            label="Name"
          />
        </FormControl>
        {/* TBD: make it nice, as for the Weekly frequency you need to select only day... in other hand you need to select first occurance anyway, where the hell UX specialists when you need one :rofl: */}
        <FormControl variant="outlined" sx={{ minWidth: 250, m:1 }} size="small"> 
          <InputLabel htmlFor={"frequency-" + this.props.id}>Frequency </InputLabel>
          <Select
            id={"frequency-" + this.props.id}
            value={this.state.payment?.frequency ?? ''}
            onChange={this.onFrequencyChange.bind(this)}
            variant="outlined"
            label="Frequency"
            labelId="frequency-label"
            startAdornment={
              <InputAdornment position="start">
                <TimerIcon />
              </InputAdornment>
            }
          >
            <MenuItem value={RecurrenceEnum.None} >One Time </MenuItem>
            <MenuItem value={RecurrenceEnum.Day}>Daily</MenuItem> 
            <MenuItem value={RecurrenceEnum.Week}>Weekly </MenuItem>
            <MenuItem value={RecurrenceEnum.Month} >Monthly </MenuItem>
            <MenuItem value={RecurrenceEnum.Year} >Yearly </MenuItem>
          </Select>
        </FormControl>
        <FormControl sx={{ minWidth: 120, m:1 }} size="small"  variant="outlined">
          {/* <InputLabel htmlFor="date">Date</InputLabel> */}
          <DatePicker
            label={this.state.payment.frequency === RecurrenceEnum.None ? 'Date' : 'First occurance'}
            renderInput={(params) => <TextField size="small"  {...params} />}
            value={this.state.payment?.date ?? null}
            onChange={this.onDateChange.bind(this)}
          />
        </FormControl>
        { this.props.onRemove &&
        <FormControl sx={{ m: 1 }} size="medium"  variant="outlined" >
          <IconButton aria-label="Remove" color="error" onClick={() => this.props.onRemove ? this.props.onRemove(this.props.id) : undefined}>
            <DeleteIcon fontSize="inherit" />
          </IconButton>
        </FormControl>
        }
        <FormControl sx={{ m: 1 }} size="medium"  variant="outlined" >
          <IconButton aria-label="Add" onClick={() => this.props.onAdd ? this.props.onAdd(this.props.id) : undefined}>
            <AddIcon fontSize="inherit" />
          </IconButton>
        </FormControl>
      </div>
    </Box>
    </LocalizationProvider>
    </>;
  }
}
